.Header {
    grid-area: header;
    display: grid;
    background-color: #02007F;
}

.Header span,button {
    text-align: center;
    margin: auto 0;
    color: white;
}

.Header span {
    font-size: 1.1em;
}

.Header button {
    font-size: 0.9em;
}

.Header span.room {
    font-size: 1.5em;
    font-style: italic;
}

.Header button {
    margin: 10px;
    font-weight: 700;
    color: black;
    border-radius: 0;
}

.Header button:hover {
    background-color: #808080;
}

@media screen and (max-width:1023px) {
    .Header {
        grid-template-columns: 55px 1fr 55px;
    }

    .Header button {
        font-size: 1.1em;
    }
}

@media screen and (min-width:1024px) {
    .Header {
        grid-template-columns: 40px 1fr 40px;
    }

    .Header span.room {
        font-size: 1.2em;
    }

    .Header button {
        margin: auto;
        width: 25px;
        height: 25px;
    }
}


